<template>
	<nav id="insight-header">
		<div id="header-body" class="header-body">
			<a href="/" class="header-logo router-link-active" title="">
				<img src="/static/images/logo.png" alt="logo" />
			</a>
			<div class="rightInput">
				<input id="searchInput" placeholder="搜天猫/淘宝宝贝">
				<button id="search" @click="search">搜索</button>
			</div>
			<ul class="rightIcon">
				<li>
					<img src="/static/images/icon-time.png" alt="" /> 每天十点更新
				</li>
				<li class="app-qr" @mouseover="mouseOver" @mouseleave="mouseleave">
					<img src="/static/images/icon-phone.png" alt="" /> APP下载
					<img src="/static/images/qr.png" class="qr none"/>
				</li>
				<li>
					<img src="/static/images/icon-wechat.png" alt="" /> 关注公众号
				</li>
			</ul>
		</div>
		<div class="sub-header">
			<ul>
				<li class="active"><a href="/">首页</a></li>
				<li id="38"><a href="/classify/38">背心</a></li>
				<li id="15"><a href="/classify/15">T恤</a></li>
				<li id="16"><a href="/classify/16">连衣裙</a></li>
				<li id="17"><a href="/classify/17">衬衫</a></li>
				<li id="37"><a href="/classify/37">打底裤</a></li>
				<li id="18"><a href="/classify/18">针织衫</a></li>
				<li id="19"><a href="/classify/19">牛仔裤</a></li>
				<li id="20"><a href="/classify/20">半身裙</a></li>
				<li id="21"><a href="/classify/21">休闲裤</a></li>
				<li id="22"><a href="/classify/22">卫衣</a></li>
				<li id="23"><a href="/classify/23">外套</a></li>
			</ul>
		</div>
	</nav>
</template>

<script>
	export default {
		mounted() {
			var id = this.$route.params.id;
			$('#'+id).addClass('active').siblings().removeClass('active');
		},
		methods: {
			search() {
				var text = $('#searchInput').val();
				var url = 'https://ai.taobao.com/search/index.htm?key=' + text + '&pid=mm_54988395_23866520_79878241';
				if (text != "") {
					window.open(url);
				}
			},
			mouseOver() {
				$(".qr").removeClass("none");
			},
			mouseleave() {
				$(".qr").addClass("none");
			}
		}
	}
</script>



